// Roboto
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700;900&display=swap');
// Futura
@font-face {
    font-family: 'Futura';
    src: url('../font/FuturaCyrillicMedium.woff2') format('woff2'), url('../font/FuturaCyrillicMedium.woff') format('woff');
    font-weight: 500;
    font-style: normal;
    font-display: swap;
}

